@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    width: 100%;
    margin-top: 4rem;
}

.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;

    label{
        flex: 0 0 10rem;
    }
    input{
        all: unset;
        flex: 1;
        padding: calc(v.$separacion/4);
        border-bottom: 2px solid lighten(v.$naranja, 5);
        &:hover,
        &:focus {
            border-bottom: 1px solid darken(v.$naranja, 5);
            outline: none;
        }
        &:disabled{
            background-color: lighten(v.$negro, 5%);
            color: v.$blanco;
            cursor: not-allowed;
        }
    }
    input {
        color-scheme: dark;
    }

    input:-webkit-autofill,
    input:-webkit-autofill:hover, 
    input:-webkit-autofill:focus,
    textarea:-webkit-autofill,
    textarea:-webkit-autofill:hover,
    textarea:-webkit-autofill:focus,
    select:-webkit-autofill,
    select:-webkit-autofill:hover,
    select:-webkit-autofill:focus {
     border-bottom: 1px solid v.$naranja;
     -webkit-text-fill-color: v.$blanco;
 //    -webkit-box-shadow: 0 0 0px 1000px v.$negro inset;
     transition: background-color 5000s ease-in-out 0s;
    }    
}