@use 'base/variables' as v;
 .contenedor-alerta{
    display: flex !important;
    justify-content: space-between;
    align-items: center;    
 }
 .texto-alerta{
   width:100%;
   text-align: center;
}
 .alerta{
  padding: calc(v.$separacion/5);
  color: v.$blanco;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  font-weight: v.$bold;
  text-transform: uppercase;
  //border-radius: 10px;

  &.error{
    p{
      color:darken(v.$rojo, 20);
    }
    background-color: v.$rojo;
  }

  &.exito{
    p{
      color:darken(v.$verde, 20);
    }
    
    background-color: v.$verde;
  }

  p{
    margin: 0;
    width: 3.5rem;
    height: 3.5rem;
    padding-top:.55rem;
    text-transform: lowercase;
    font-weight: v.$bold;
    text-align: center;
  }

  .boton-cerrar-alerta-error{
    border-radius: 50%;
    cursor: pointer;
    background-color: darken($color: v.$rojo, $amount: 3);  
  }
  .boton-cerrar-alerta-exito{
    border-radius: 50%;
    cursor: pointer;
    background-color: darken($color: v.$verde, $amount: 3);  
  }
}
